body {
  font-family: "Open Sans", sans-serif;
  background-color: rgba(168, 218, 220, 0.2);
}

/* NAVIGATION CSS */
div nav a{
  font-size: 1em;
  text-decoration: none;
  color: black;
}

div nav {
  background-color: rgba(168, 218, 220, 1);
}

/* SKILLS CARD */
div .card {
  border: transparent;
  border-radius: 1em;
}

div .card-title {
  border-bottom: 3px solid rgba(230, 57, 70, 1);
  font-weight: bold;
  font-size: 1.4em;
  width: 5em;
  margin: 0em auto 0.8em auto;
  padding-bottom: 0.2em ;
}

div .card-subtitle {
  font-size: 0.9em;
}

div .cardHover {
  box-shadow: 12px 17px 15px rgba(29, 53, 87, 1);
  border-radius: 15px;
  height: 22em;
  width: 18em;
  background: rgba(255, 255, 255, 0.9);
  color:  rgba(29, 53, 87, 1);
}

div  .cardHover:hover {
  transform: scale(1.03);
}

/* PORTFOLIO IMAGE IN MAIN COMPONENT */
div .portfolioImg {
  border-radius: 15px;
  width: 18em;
  align-self: center;
}

.card-text li {
  margin-top: 0.2em;
  list-style-type: circle;
}

h3 .fa {
  font-size: 1.8em;
  margin: 0;
}

div .logo {
  width: 12em;
}

/* LINE DIVIDER FOR CONTENT */
div .Divider {
  height: 4px;
  border-radius: 5em;
  background-color:  rgba(230, 57, 70, 0.3);
  margin: 6em 6em;
}

/* JUMBOTRON HERO BANNER */
.heroBanner {
  background-color: rgba(29, 53, 87, 1);
}

/* FOOTER CSS */
div .site-footer {
  background-color: rgba(29, 53, 87, 1);
}

/* CONTACT CONTAINER CSS */
div #contactDiv {
  box-shadow: 1em 1.5em 2.5em #888;
  width: 55em;
  margin: 5em auto 5em auto;
  border-radius: 5em;
}

div .contactContainer {
  min-height: 30em;
}

.contactContainer .col-4 {
  background-color: rgba(29, 53, 87, 1);
  border-radius: 3em 0 0 3em;
}

.contactContainer .col-8 {
  background-color: rgba(230, 57, 70, 1);
  border-radius: 0 3em 3em 0;
}

.contactContainer h4 {
  color: rgba(241, 250, 238, 1);
  margin-top: 1em;
  margin-bottom: 2em;
  font-size: 3em;
}

.contactContainer #sendButton {
  color: rgba(241, 250, 238, 1);
  margin-top: 0.2em;
  margin-bottom: 2em;
  font-size: 0.7em;
}

div .contactInput {
  margin: 0em auto 1em auto;
  width: 15em;
  font-size: 1em;
  color: rgba(241, 250, 238, 1);
  height: 2.5em;
  border: 0;
  outline: 0;
  background: transparent;
  border-radius: 0em;
  border-bottom: 1px solid rgba(241, 250, 238, 1);
}

div .inputText {
  margin: 3em auto 0.6em auto;
  height: 9.5em;
  font-size: .8em;
  border-radius: .3em;
  border: 1px solid grey;
  background-color: #f0f0f0;
}